.listContainer{
    display: flex;
    justify-content: center;
    margin-top: 20px;

}

.listWrapper{
    width: 100%;
    max-width: 1024px;
    display: flex;
    gap: 20px;
}

.listSearch{
    flex: 1;
    background-color: aquamarine;
    padding: 10px;
    border-radius: 10px;
    position: sticky;
    top: 10px;
    height: max-content;
}

.lsTitle{
    font-size: 20px;
    color: purple;
    margin-bottom: 10px;
}

.lsItem{
    display: flex;
    flex-direction: column;
    gap: 5px;
    margin-bottom: 10px;
}

.lsItem>label{
    font-size: 12px;
}

.lsItem>input{
    height: 30px;
    border: none;
    padding: 5px;
}

.lsItem>span{
    height: 30px;
    padding: 5px;
    background-color: white;
    display: flex;
    align-items: center;
    cursor: pointer;
}
 
.lsOptions{
    padding: 10px;
}

.lsOptionItem{
    display: flex;
    justify-content: space-between;
    margin-bottom: 10px;
    color: purple;
    font-size: 12px;
}

.siMN{
    color: blue;
    font-weight: bold;
    cursor: pointer;
    margin-top: 10px;
}

.lsOptionInput{
    width: 80px;
}

.listSearch>button{
    padding: 10px; 
    background: linear-gradient(90deg, black , purple);
    color: white;
    border: none;
    border-radius: 15px;
    width: 100%;
    font-weight: bold;
    cursor: pointer;
}